import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { userModel } from "src/app/models/user.model";
import { APIServiceService } from "../../../services/api-service.service";
import Swal from "sweetalert2";
import { chatRoomModel } from "../../../models/chat-room.model";

@Component({
  selector: "app-profile",
  templateUrl: "./profile.component.html",
  styleUrls: ["./profile.component.css"]
})
export class ProfileComponent implements OnInit {
  userId: string;
  Usuario: userModel;
  editable = false;
  amigos = false;
  imagen;
  publicaciones: any[] = [];
  actualUser: string;
  externalUser: userModel;
  chatRoom: chatRoomModel;
  interval;
  p = 1;

  constructor(
    private activatedRoute: ActivatedRoute,
    private api: APIServiceService,
    private router: Router
  ) {
    this.activatedRoute.params.subscribe(params => {
      this.userId = params.id;
      this.publicaciones = [];
      this.getUser();
      this.getPublications();
      this.DoFollow();
    });
    this.Usuario = new userModel();
    this.externalUser = new userModel();
    this.chatRoom = new chatRoomModel();
    this.imagen = true;
  }

  ngOnInit() {
    this.getUser();
    this.getPublications();
    this.DoFollow();
  }

  getUser() {
    this.api
      .getElement(this.userId, "http://localhost:3000/api/users")
      .subscribe(res => {
        this.Usuario.Bdate = res["Usuario"].Bdate;
        this.Usuario.email = res["Usuario"].email;
        this.Usuario.lastname = res["Usuario"].lastname;
        this.Usuario.name = res["Usuario"].name;
        this.Usuario.open = res["Usuario"].open;
        this.Usuario.password = res["Usuario"].password;
        this.Usuario._id = res["Usuario"]._id;
        this.Usuario.friendList = res["Usuario"].friendList;
        this.Usuario.img = res["Usuario"].img;
        this.Usuario.level = res["Usuario"].level;
        this.Usuario.uname = res["Usuario"].uname;
        this.Usuario.cdate = res["Usuario"].cdate;
        this.Usuario.followList = res["Usuario"].followList;
        if (this.Usuario._id === localStorage.getItem("_id")) {
          this.editable = true;
        }
      });
  }

  close() {
    if (this.Usuario._id === localStorage.getItem("_id")) {
      this.imagen = true;
    } else {
      this.imagen = true;
    }
  }

  open() {
    this.getUser();
    if (this.Usuario._id === localStorage.getItem("_id")) {
      this.imagen = false;
    } else {
      this.imagen = true;
    }
  }

  cambiarImagen(url: string) {
    if (this.Usuario._id === localStorage.getItem("_id")) {
      this.Usuario.img = url;
      this.guardar();
    } else {
      $(".cambiarImagen").hide();
    }
  }

  saveChanges() {
    this.Usuario.uname = $("#uname").html();

    this.api
      .getElemets("http://localhost:3000/api/users")
      .subscribe((res: Object[]) => {
        for (const i of res) {
          if (i["uname"] === this.Usuario.uname) {
            if (i["_id"] !== this.Usuario._id) {
              Swal.fire({
                allowOutsideClick: false,
                type: "error",
                text: "El nombre de usuario ya está en uso"
              });
              return;
            }
          }
        }
      });
    this.guardar();
  }

  guardar() {
    this.api
      .modifyUser(this.Usuario, "http://localhost:3000/api/users")
      .subscribe(() => {
        Swal.fire({
          allowOutsideClick: false,
          type: "success",
          text: "Cambios realizados con éxito"
        });
      });
  }

  getPublications() {
    this.api
      .getElemets("http://localhost:3000/api/publications")
      .subscribe((res: Object[]) => {
        for (const i of res) {
          if (i["id_user"] === this.userId) {
            if (i["game_room_id"] === " ") {
              this.publicaciones.unshift(i);
            }
          }
        }
      });
  }

  addFriend() {
    this.actualUser = localStorage.getItem("_id");

    this.api
      .getElement(this.actualUser, "http://localhost:3000/api/users")
      .subscribe(res => {
        this.externalUser.Bdate = res["Usuario"].Bdate;
        this.externalUser._id = res["Usuario"]._id;
        this.externalUser.email = res["Usuario"].email;
        this.externalUser.friendList = res["Usuario"].friendList;
        this.externalUser.img = res["Usuario"].img;
        this.externalUser.lastname = res["Usuario"].lastname;
        this.externalUser.level = res["Usuario"].level;
        this.externalUser.name = res["Usuario"].name;
        this.externalUser.open = res["Usuario"].open;
        this.externalUser.password = res["Usuario"].password;
        this.externalUser.uname = res["Usuario"].uname;
        this.externalUser.cdate = res["Usuario"].cdate;
        this.externalUser.followList = res["Usuario"].followList;
        /*  */
        this.getUser();
        this.externalUser.friendList.push(this.userId);
        this.api
          .modifyUser(this.externalUser, "http://localhost:3000/api/users")
          .subscribe(() => {
            /* AÑADIR A LA LISTA DEL FOLLOW DEL DUEÑO DEL PERFIL */
            this.Usuario.followList.push(this.externalUser._id);
            this.api
              .modifyUser(this.Usuario, "http://localhost:3000/api/users")
              .subscribe(() => {
                this.DoFollow();
              });
            /*  */
          });
      });
  }

  removeFriend() {
    this.actualUser = localStorage.getItem("_id");

    this.api
      .getElement(this.actualUser, "http://localhost:3000/api/users")
      .subscribe(res => {
        this.externalUser.Bdate = res["Usuario"].Bdate;
        this.externalUser._id = res["Usuario"]._id;
        this.externalUser.email = res["Usuario"].email;
        this.externalUser.friendList = res["Usuario"].friendList;
        this.externalUser.img = res["Usuario"].img;
        this.externalUser.lastname = res["Usuario"].lastname;
        this.externalUser.level = res["Usuario"].level;
        this.externalUser.name = res["Usuario"].name;
        this.externalUser.open = res["Usuario"].open;
        this.externalUser.password = res["Usuario"].password;
        this.externalUser.uname = res["Usuario"].uname;
        this.externalUser.cdate = res["Usuario"].cdate;
        this.externalUser.followList = res["Usuario"].followList;
        /*  */
        this.getUser();
        for (let i = 0; i < this.externalUser.friendList.length; i++) {
          if (this.externalUser.friendList[i] === this.userId) {
            this.externalUser.friendList.splice(i, 1);
          }
        }
        this.api
          .modifyUser(this.externalUser, "http://localhost:3000/api/users")
          .subscribe(() => {
            /* REMOVER DE LA LISTA DEL FOLLOW DEL DUEÑO DEL PERFIL */
            for (let i = 0; i < this.Usuario.followList.length; i++) {
              if (this.Usuario.followList[i] === this.externalUser._id) {
                this.Usuario.followList.splice(i, 1);
              }
            }
            this.api
              .modifyUser(this.Usuario, "http://localhost:3000/api/users")
              .subscribe(() => {
                this.DoFollow();
              });
            /*  */
          });
      });
  }

  DoFollow() {
    this.actualUser = localStorage.getItem("_id");

    this.api
      .getElement(this.actualUser, "http://localhost:3000/api/users")
      .subscribe(res => {
        this.externalUser.Bdate = res["Usuario"].Bdate;
        this.externalUser._id = res["Usuario"]._id;
        this.externalUser.email = res["Usuario"].email;
        this.externalUser.friendList = res["Usuario"].friendList;
        this.externalUser.img = res["Usuario"].img;
        this.externalUser.lastname = res["Usuario"].lastname;
        this.externalUser.level = res["Usuario"].level;
        this.externalUser.name = res["Usuario"].name;
        this.externalUser.open = res["Usuario"].open;
        this.externalUser.password = res["Usuario"].password;
        this.externalUser.uname = res["Usuario"].uname;
        this.externalUser.cdate = res["Usuario"].cdate;
        this.externalUser.followList = res["Usuario"].followList;
        /*  */
        this.amigos = false;
        for (const a of this.externalUser.friendList) {
          if (a === this.userId) {
            this.amigos = true;
          }
        }
      });
  }

  sendMessage() {
    this.chatRoom.id_users.push(this.externalUser._id);
    this.chatRoom.id_users.push(this.Usuario._id);

    this.api
      .getElemets("http://localhost:3000/api/chatrooms")
      .subscribe((res: Object[]) => {
        let contador: number = 0;

        for (let a of res) {
          contador = 0;
          for (let b of a["id_users"]) {
            if (this.chatRoom.id_users.indexOf(b) !== -1) {
              contador += 1;
              if (contador > 1) {
                this.chatRoom._id = a["_id"];
              }
            }
          }
        }
        if (this.chatRoom._id === undefined) {
          this.api
            .createChatRoom(
              this.chatRoom,
              "http://localhost:3000/api/chatrooms"
            )
            .subscribe(res => {
              console.log(res);
              this.sendMessage();
            });
        } else {
          this.router.navigate(["chat", this.chatRoom._id]);
        }
      });
  }
}
