import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { userModel } from '../../../models/user.model';
import { APIServiceService } from '../../../services/api-service.service';
import { Router } from '@angular/router';
import * as $ from 'jquery';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-logger',
  templateUrl: './logger.component.html',
  styleUrls: ['./logger.component.css']
})
export class LoggerComponent implements OnInit {

  usuario: userModel;
  passes: Boolean;
  validname: Boolean;
  validmail: Boolean;
  usuarioCorrecto: Boolean;
  users_names: String[] = [];
  users_mails: String[] = [];
  usuarios: any[] = [];
  id_usuario: string;

  constructor( private userService: APIServiceService, private router: Router) {
    this.isConnected();
  }

  ngOnInit() {
    this.jquery();

    this.usuario = new userModel();
    this.getAllusers();
  }

  jquery() {
        /* LOGIN - SING UP */

        $('.singup-container-c').hide();

        $('.singup-cliker').on('click', function() {
          $('.secction-login-c').css('min-height', '850px');
          $('.singup-container-c').slideDown('slow');
        });

        $('.login-cliker').on('click', function() {
          $('.singup-container-c').slideUp('slow');
          setTimeout(function() {
            $('.secction-login-c').css('min-height', '600px');
          }, 500);
        });

        /* LOGIN - SING UP */
  }

  onSubmitReg( form: NgForm ) {
    this.getAllusers();
    this.validmail = true;
    this.validname = true;
    this.passes = true;

    if ( form.invalid ) {
      return;
    }

    Swal.fire({
      allowOutsideClick: false,
      type: 'info',
      text: 'Espere por favor'
    });
    Swal.showLoading();

    if (this.usuario.password !== this.usuario.repassword) {
      this.passes = false;
      Swal.fire({
        allowOutsideClick: false,
        type: 'error',
        text: 'Las contraseñas no son iguales'
      });
      return;
    }

    for (const m of this.users_mails) {
      if (this.usuario.email == m) {
        this.validmail = false;
        Swal.fire({
          allowOutsideClick: false,
          type: 'error',
          text: 'El mail está en uso'
        });
        return;
      }
    }

    for (const n of this.users_names) {
      if (this.usuario.uname == n) {
        Swal.fire({
          allowOutsideClick: false,
          type: 'error',
          text: 'El nombre de usuario está en uso'
        });
        this.validname = false;
        return;
      }
    }

    console.log(this.usuario);
    this.userService.createUser( this.usuario , 'http://localhost:3000/api/users')
    .subscribe(res => {
      Swal.fire({
        allowOutsideClick: false,
        type: 'success',
        text: 'Se ha creado con éxito'
      });
      $('.singup-container-c').slideUp('slow');
      setTimeout(function() {
          $('.secction-login-c').css('min-height', '80vh');
        }, 500);
    });


  }

  onSubmitLog( form: NgForm ) {
    this.getAllusers();

    this.usuarioCorrecto = false;


    if ( form.invalid ) {
      return;
    }

    Swal.fire({
      allowOutsideClick: false,
      type: 'info',
      text: 'Espere por favor'
    });
    Swal.showLoading();

    for (const a of this.usuarios) {
      if (this.usuario.uname === a.uname && this.usuario.password === a.password) {
        this.usuarioCorrecto = true;
        this.id_usuario = a._id;
      }
    }

    if (this.usuarioCorrecto) {
      localStorage.setItem('_id', this.id_usuario);
      Swal.fire({
        allowOutsideClick: false,
        type: 'success',
        text: 'Los datos son correctos'
      });
      this.router.navigate(['/timeline']);

    } else {
      Swal.fire({
        allowOutsideClick: false,
        type: 'error',
        text: 'El nombre de usuario y contraseña no cohinciden'
      });
      return;
    }

  }

  getAllusers() {
    this.users_names.slice(0);
    this.users_mails.slice(0);
    this.usuarios.slice(0);
    this.userService.getElemets('http://localhost:3000/api/users')
    .subscribe( (res: Object[]) => {
      for (let i of res) {
        this.users_names.push(i['uname']);
        this.users_mails.push(i['email']);
        this.usuarios.push(i);
      }
    });
  }

  isConnected() {
    this.id_usuario = localStorage.getItem('_id');
    if(this.id_usuario != null){
      this.router.navigate(['/timeline']);
    }
  }

}
