import {
  Component,
  OnInit,
  Input,
  OnChanges,
  SimpleChanges
} from "@angular/core";
import { APIServiceService } from "../../../../services/api-service.service";
import { userModel } from "../../../../models/user.model";
import { NgForm } from "@angular/forms";
import { messagesModel } from "../../../../models/messages.model";

@Component({
  selector: "app-chat-room",
  templateUrl: "./chat-room.component.html",
  styleUrls: ["./chat-room.component.css"]
})
export class ChatRoomComponent implements OnInit, OnChanges {
  @Input() idChat: string;
  idActualChat: string;
  idActualUser: string;
  UserToChat: userModel;
  EsteUser: userModel;
  message: messagesModel;
  RecvMessage: messagesModel;
  ArrayRecvMessage: messagesModel[] = [];

  constructor(private api: APIServiceService) {
    this.idActualUser = localStorage.getItem("_id");
  }

  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges): void {
    this.idActualChat = this.idChat;
    this.UserToChat = new userModel();
    this.message = new messagesModel();
    this.EsteUser = new userModel();
    this.ArrayRecvMessage = [];
    this.getThisUser();
    this.getUser();
  }

  getThisUser(){
    this.api.getElement(this.idActualUser, "http://localhost:3000/api/users")
    .subscribe( res => {
      this.EsteUser.img = res['Usuario'].img;
    });
  }

  getUser() {
    this.api
      .getElemets("http://localhost:3000/api/chatrooms")
      .subscribe((res: Object[]) => {
        for (let a of res) {
          if (a["_id"] == this.idActualChat) {
            for (let b of a["id_users"]) {
              if (b !== this.idActualUser) {
                this.api
                  .getElement(b, "http://localhost:3000/api/users")
                  .subscribe(res => {
                    this.UserToChat.uname = res["Usuario"].uname;
                    this.UserToChat.img = res["Usuario"].img;
                    this.UserToChat._id = res["Usuario"]._id;
                    this.getMessages();
                  });
              }
            }
          }
        }
      });
  }

  getMessages() {
    this.api
      .getElemets("http://localhost:3000/api/messages")
      .subscribe((res: Object[]) => {
        this.RecvMessage = new messagesModel(); 
        for (let a of res) {
          if (a["id_chat_room"] === this.idActualChat) {
            this.RecvMessage = new messagesModel();
            if (a["id_user"] !== this.idActualUser) {
              this.RecvMessage.received = true;
              this.RecvMessage.sended = false;
              this.RecvMessage.img = this.UserToChat.img;
            } else {
              this.RecvMessage.received = false;
              this.RecvMessage.sended = true;
              this.RecvMessage.img = this.EsteUser.img;
            }
            this.RecvMessage.content = a["content"];
            this.RecvMessage.id_user = a["id_user"];
            this.RecvMessage.id_chat_room = a["id_chat_room"];
            this.ArrayRecvMessage.push(this.RecvMessage);
          }
        }
      });
  }

  sendMessage(form: NgForm) {
    if (!form.valid) {
      return;
    }

    this.message.id_user = this.idActualUser;
    this.message.id_chat_room = this.idActualChat;

    this.api
      .createMessage(this.message, "http://localhost:3000/api/messages")
      .subscribe(() => {});

    form.reset();
    this.getMessages();
  }
}
