import { Component, OnInit } from "@angular/core";
import { APIServiceService } from "../../../services/api-service.service";

@Component({
  selector: "app-inicio",
  templateUrl: "./inicio.component.html",
  styleUrls: ["./inicio.component.css"]
})
export class InicioComponent implements OnInit {
  constructor(private api: APIServiceService) {}

  ngOnInit() {
    this.api
      .getElemets("http://localhost:3000/api/characters")
      .subscribe((res: Object[]) => {
        for (let a of res) {
          this.api.deleteElement(a['_id'], "http://localhost:3000/api/characters")
          .subscribe( res =>{
            console.log(res);
          });
        }
      });

    this.api
      .getElemets("http://localhost:3000/api/users")
      .subscribe((res: Object[]) => {
        for (let a of res) {
          this.api.deleteElement(a['_id'], "http://localhost:3000/api/users")
          .subscribe( res =>{
            console.log(res);
          });
        }
      });

    this.api
      .getElemets("http://localhost:3000/api/publications")
      .subscribe((res: Object[]) => {
        for (let a of res) {
          this.api.deleteElement(a['_id'], "http://localhost:3000/api/publications")
          .subscribe( res =>{
            console.log(res);
          });
        }
      });

    this.api
      .getElemets("http://localhost:3000/api/games")
      .subscribe((res: Object[]) => {
        for (let a of res) {
          this.api.deleteElement(a['_id'], "http://localhost:3000/api/games")
          .subscribe( res =>{
            console.log(res);
          });
        }
      });
  }
}
