import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* PUBLIC */
import { HomeComponent } from './components/public/home/home.component';
import { ContactComponent } from './components/public/contact/contact.component';
import { LoggerComponent } from './components/public/logger/logger.component';
/* BLOG */
import { BlogHomeComponent } from './components/public/blog/blog-home/blog-home.component';
import { BlogCategoryComponent } from './components/public/blog/blog-category/blog-category.component';
import { BlogSearchComponent } from './components/public/blog/blog-search/blog-search.component';
import { BlogPublicationPageComponent } from './components/public/blog/blog-publication-page/blog-publication-page.component';
/* 404 */
import { NotFoundComponentComponent } from './components/public/not-found-component/not-found-component.component';
/* PRIVATE */
import { TimelineComponent } from './components/private/timeline/timeline.component';
import { MyBaultComponent } from './components/private/baul/my-bault/my-bault.component';
import { CharactersComponent } from './components/private/baul/characters/characters.component';
import { GamesComponent } from './components/private/baul/games/games.component';
import { SearchComponent } from './components/private/search/search.component';
import { ProfileComponent } from './components/private/profile/profile.component';
import { InicioComponent } from './components/administracion/inicio/inicio.component';
import { ChatComponent } from './components/private/chat/chat.component';
import { ChatRoomComponent } from './components/private/chat/chat-room/chat-room.component';

const ROUTES: Routes = [
  /* PUBLIC */
  { path: 'home', component: HomeComponent },
  { path: 'contact', component: ContactComponent },
  { path: 'logger', component: LoggerComponent },
  /* BLOG */
  { path: 'blog', component: BlogHomeComponent },
  { path: 'blog/:category', component: BlogCategoryComponent },
  { path: 'blog/publicaciones/:id_public', component: BlogPublicationPageComponent },
  { path: 'blog/search/:term', component: BlogSearchComponent },
  /* PRIVATE */
  { path: 'timeline', component: TimelineComponent },
  { path: 'baul', component: MyBaultComponent },
  { path: 'personaje/:id', component: CharactersComponent },
  { path: 'partida/:id', component: GamesComponent },
  { path: 'search/:term', component: SearchComponent},
  { path: 'profile/:id', component: ProfileComponent },
  { path: 'chat/:id', component: ChatComponent },
  /* ADMIN */
  { path: 'admin', component: InicioComponent },
  /* EXTRAS */
  { path: '', pathMatch: 'full', redirectTo: 'home' },
  { path: '**', component: NotFoundComponentComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(ROUTES, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
