import { Component, OnDestroy, OnInit } from "@angular/core";
import { userModel } from "src/app/models/user.model";
import { APIServiceService } from "../../../services/api-service.service";

@Component({
  selector: "app-timeline",
  templateUrl: "./timeline.component.html",
  styleUrls: ["./timeline.component.css"]
})
export class TimelineComponent implements OnInit, OnDestroy {
  userId: string;
  user: userModel;
  publicaciones: any[] = [];
  contador: number = 0;
  p: number = 1;
  interval;
  empty: boolean = false;

  constructor(private api: APIServiceService) {
    this.user = new userModel();
    this.getUserId();
  }

  ngOnInit() {}

  ngOnDestroy(): void {
    if (this.interval) {
      clearInterval(this.interval);
    }
  }

  getUserId() {
    this.userId = localStorage.getItem("_id");
    this.getUserData();
    this.interval = setInterval(() => {
      this.getUserData();
    }, 10000);
  }

  getUserData() {
    this.api
      .getElement(this.userId, "http://localhost:3000/api/users")
      .subscribe(res => {
        this.user._id = res["Usuario"]._id;
        this.user.name = res["Usuario"].uname;
        this.user.img = res["Usuario"].img;
        this.user.friendList = res["Usuario"].friendList;
        /*
         */
        this.api
          .getElemets("http://localhost:3000/api/publications")
          .subscribe((res: Object[]) => {
            for (let i of res) {
              if (i["id_user"] == this.userId) {
                let not = false;
                for (let pub of this.publicaciones) {
                  if (pub["_id"] === i["_id"]) {
                    not = true;
                  }
                }
                if (i["game_room_id"] !== " ") {
                  not = true;
                }
                if (not === false) {
                  this.contador += 1;
                  this.publicaciones.unshift(i);
                }
              } else {
                for (let a of this.user.friendList) {
                  if (i["id_user"] === a) {
                    let not = false;
                    for (let pub of this.publicaciones) {
                      if (pub["_id"] === i["_id"]) {
                        not = true;
                      }
                    }
                    if (i["game_room_id"] !== " ") {
                      not = true;
                    }
                    if (not === false) {
                      this.publicaciones.unshift(i);
                    }
                  }
                }
              }
            }
            if (this.publicaciones.length === 0) {
              this.empty = true;
            } else {
              this.empty = false;
            }
          });
      });
  }
}
