import { Component, OnInit, Input } from '@angular/core';
import { APIServiceService } from '../../../services/api-service.service';
import { fichaModel } from '../../../models/fichas.model';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-ficha',
  templateUrl: './ficha.component.html',
  styleUrls: ['./ficha.component.css']
})
export class FichaComponent implements OnInit {

  @Input() id: string;
  ficha: fichaModel;
  editable: boolean;
  divChange: boolean = false; 

  constructor(private api: APIServiceService) { 
    this.ficha = new fichaModel();
  }

  ngOnInit() {
    this.getInfo();
  }

  getInfo(){
    this.api.getElement(this.id, "http://localhost:3000/api/files")
    .subscribe( res =>{
      this.ficha._id = res['Usuario']._id;
      this.ficha.content = res['Usuario'].content;
      this.ficha.id_character = res['Usuario'].id_character;
      this.ficha.id_user = res['Usuario'].id_user;
      this.ficha.title = res['Usuario'].title;
      if(this.ficha.id_user == localStorage.getItem("_id")){
        this.editable = true;
      }else{
        this.editable = false;
      }
    });
  }

  edit(){
    this.ficha.title = $("#titulo").html();
    this.save();
  }

  save(){
    this.api.modifyfichas(this.ficha, "http://localhost:3000/api/files")
    .subscribe(() =>{
    });
  }

  openChangeImage(){
    this.divChange = true;
  }

  closeChangeImage(){
    this.divChange = false;
  }

  changeImage(value: string){
    this.ficha.content = value;
    this.save();
  }

}
