import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { publicationModel } from 'src/app/models/publications.model';
import { APIServiceService } from '../../../services/api-service.service';
import Swal from 'sweetalert2';
import { userModel } from 'src/app/models/user.model';

@Component({
  selector: 'app-nueva-publicacion',
  templateUrl: './nueva-publicacion.component.html',
  styleUrls: ['./nueva-publicacion.component.css']
})
export class NuevaPublicacionComponent implements OnInit {

  nuevaPublicacion: publicationModel;
  Usuario: userModel;

  constructor( private api: APIServiceService) { 
    this.nuevaPublicacion = new publicationModel();
    this.Usuario = new userModel();
    this.getUser();
  }

  ngOnInit() {
    $(document).ready(function() {
      $(".new-back").hide();

      $(".cerrar").on("click", function() {
        $(".new-back").hide();
      });
      $(".new-public-c button").on("click", function() {
        $(".new-back").show();
      });
    }); 
  }

  crearPublicacion( form: NgForm ){
    if(form.invalid){
      return;
    }

    /* console.log(this.nuevaPublicacion); */
    this.nuevaPublicacion.id_user = localStorage.getItem("_id");
    this.nuevaPublicacion.game_room_id = " ";
    this.nuevaPublicacion.id_character = " ";

    this.api.createPublication(this.nuevaPublicacion, 'http://localhost:3000/api/publications')
    .subscribe(res =>{
      Swal.fire({
        allowOutsideClick: false,
        type: 'success',
        text: 'Listo!'
      });
    });

  }

  getUser() {
    this.api
      .getElement(localStorage.getItem("_id"), "http://localhost:3000/api/users")
      .subscribe(res => {
        this.Usuario.Bdate = res["Usuario"].Bdate;
        this.Usuario.email = res["Usuario"].email;
        this.Usuario.lastname = res["Usuario"].lastname;
        this.Usuario.name = res["Usuario"].name;
        this.Usuario.open = res["Usuario"].open;
        this.Usuario.password = res["Usuario"].password;
        this.Usuario._id = res["Usuario"]._id;
        this.Usuario.friendList = res["Usuario"].friendList;
        this.Usuario.img = res["Usuario"].img;
        this.Usuario.level = res["Usuario"].level;
        this.Usuario.uname = res["Usuario"].uname;
        this.Usuario.cdate = res["Usuario"].cdate;
        this.Usuario.followList = res["Usuario"].followList;
      });
  }

}
