import { Component, OnInit, OnDestroy } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { APIServiceService } from "src/app/services/api-service.service";
import { gameRoomModel } from "../../../../models/game-room.model";
import { userModel } from "../../../../models/user.model";
import { publicationModel } from "../../../../models/publications.model";

@Component({
  selector: "app-games",
  templateUrl: "./games.component.html",
  styleUrls: ["./games.component.css"]
})
export class GamesComponent implements OnInit, OnDestroy {
  idPartida: string;
  partida: gameRoomModel;
  master: userModel;
  divImagen: boolean = false;
  divPass: boolean = false;
  divJoin: boolean = false;
  editable: boolean = false;
  cpass: boolean = false;
  message: boolean = false;
  joined: boolean = false;
  disabled: string;

  publicaciones: any[] = [];
  characters: any[] = [];
  MyCharacters: any[] = [];
  nuevaPublicacion: publicationModel;
  p: number = 1;
  pc: number = 1;
  interval;

  constructor(
    private activatedRoute: ActivatedRoute,
    private api: APIServiceService,
    private router: Router
  ) {
    this.activatedRoute.params.subscribe(params => {
      this.idPartida = params.id;
    });
    this.partida = new gameRoomModel();
    this.master = new userModel();
    this.nuevaPublicacion = new publicationModel();
  }

  ngOnInit() {
    this.getPartida();
    $(document)
      .one("focus.autoExpand", "textarea.autoExpand", function() {
        var savedValue = this.value;
        this.value = "";
        this.baseScrollHeight = this.scrollHeight;
        this.value = savedValue;
      })
      .on("input.autoExpand", "textarea.autoExpand", function() {
        var minRows = this.getAttribute("data-min-rows") | 0,
          rows;
        this.rows = minRows;
        rows = Math.ceil((this.scrollHeight - this.baseScrollHeight) / 16);
        this.rows = minRows + rows;
      });
  }

  getPartida() {
    this.api
      .getElement(this.idPartida, "http://localhost:3000/api/games")
      .subscribe(res => {
        this.partida.title = res["Usuario"].title;
        this.partida._id = res["Usuario"]._id;
        this.partida.description = res["Usuario"].description;
        this.partida.id_user = res["Usuario"].id_user;
        this.partida.img = res["Usuario"].img;
        this.partida.open = res["Usuario"].open;
        this.partida.password = res["Usuario"].password;
        this.partida.players = res["Usuario"].players;
        this.disabled = "";
        if (this.partida.description == null) {
          this.partida.description = "Descripción";
        }
        if (this.partida.password !== "") {
          this.cpass = true;
        } else {
          this.cpass = false;
        }
        if (this.partida.id_user === localStorage.getItem("_id")) {
          this.editable = true;
          this.cpass = false;
          this.joined = true;
        } else {
          this.disabled = "disabled";
          this.getMyCharacters();
        }
        this.getMaster();
        this.getPublications();
        this.interval = setInterval(() => {
          this.getPublications();
          this.getPlayers();
        }, 10000);
      });
  }

  getMaster() {
    this.api
      .getElement(this.partida.id_user, "http://localhost:3000/api/users")
      .subscribe(res => {
        this.master.uname = res["Usuario"].uname;
        this.master.img = res["Usuario"].img;
        this.master.friendList = res["Usuario"].friendList;
      });
  }

  getPlayers() {
    for (let a of this.partida.players) {
      this.api
        .getElement(a, "http://localhost:3000/api/characters")
        .subscribe(res => {
          this.characters.push(res["Usuario"]);
        });
    }
  }

  /*  */

  saveChanges() {
    this.partida.title = $("#titulo").html();
    this.partida.description = $("#descrip").html();
    if ($("#open").is(":Checked")) {
      this.partida.open = true;
    } else {
      this.partida.open = false;
    }
    this.guardar();
  }

  delete() {
    this.api
      .deleteElement(this.partida._id, "http://localhost:3000/api/games")
      .subscribe(() => {
        this.router.navigate(["/baul"]);
      });
  }

  guardar() {
    this.api
      .modifyGameRoom(this.partida, "http://localhost:3000/api/games")
      .subscribe(() => {});
  }

  crearNuevaPublicacion(texto: string) {
    this.nuevaPublicacion.content = texto;
    this.nuevaPublicacion.game_room_id = this.partida._id;
    this.nuevaPublicacion.id_character = localStorage.getItem("id_character");
    if (this.nuevaPublicacion.id_character == null) {
      this.nuevaPublicacion.id_character = " ";
    }
    this.nuevaPublicacion.id_user = localStorage.getItem("_id");
    this.api
      .createPublication(
        this.nuevaPublicacion,
        "http://localhost:3000/api/publications"
      )
      .subscribe(() => {
        this.getPublications();
      });
  }

  getPublications() {
    this.api
      .getElemets("http://localhost:3000/api/publications")
      .subscribe((res: Object[]) => {
        for (let a of res) {
          if (a["game_room_id"] === this.partida._id) {
            let not = false;
            for (let pub of this.publicaciones) {
              if (pub["_id"] === a["_id"]) {
                not = true;
              }
            }
            if (not === false) {
              this.publicaciones.unshift(a);
            }
          }
        }
      });
  }

  getMyCharacters() {
    this.api
      .getElemets("http://localhost:3000/api/characters")
      .subscribe((res: object[]) => {
        for (let a of res) {
          this.MyCharacters.push(a);
        }
        this.isMyCharacter();
      });
  }

  selectCharacter(id: string) {
    this.partida.players.push(id);
    this.divJoin = false;
    this.joined = true;
    this.guardar();
  }

  isMyCharacter() {
    for (let a of this.partida.players) {
      for (let i of this.MyCharacters) {
        if (a === i._id) {
          this.joined = true;
          localStorage.setItem("id_character", i._id);
        }
      }
    }
  }

  leaveRoom() {
    let character = localStorage.getItem("id_character");
    for (let i = 0; i < this.partida.players.length; i++) {
      if (this.partida.players[i] === character) {
        this.joined = false;
        this.partida.players.splice(i, 1);
      }
    }
    this.guardar();
  }

  /*  */

  tryPass(pass: string) {
    if (this.partida.password === pass) {
      this.cpass = false;
      this.message = false;
    } else {
      this.message = true;
    }
  }

  cambiarImagen(url: string) {
    this.partida.img = url;
    this.guardar();
  }

  cambiarPass(pass: string) {
    this.partida.password = pass;
    this.closeChangePass();
    this.guardar();
  }

  openChangeImage() {
    this.divImagen = true;
  }

  closeChangeImage() {
    this.divImagen = false;
  }

  openChangePass() {
    this.divPass = true;
  }

  closeChangePass() {
    this.divPass = false;
  }

  openJoin() {
    this.divJoin = true;
  }

  closeJoin() {
    this.divJoin = false;
  }

  ngOnDestroy(): void {
    localStorage.removeItem("id_character");
    if (this.interval) {
      clearInterval(this.interval);
    }
  }
}
