import { Component, OnInit} from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { APIServiceService } from "../../../services/api-service.service";

@Component({
  selector: "app-search",
  templateUrl: "./search.component.html",
  styleUrls: ["./search.component.css"]
})
export class SearchComponent implements OnInit {
  keyWord: string;
  coincidences: any[] = [];

  constructor(
    private activatedRoute: ActivatedRoute,
    private api: APIServiceService
  ) {
    this.activatedRoute.params.subscribe(res => {
      this.keyWord = res.term;
      this.coincidences = [];
      this.getAllResults();
    });
    
  }

  ngOnInit() {
  }

  getAllResults() {
    this.api
      .getElemets("http://localhost:3000/api/characters")
      .subscribe((res: Object[]) => {
        for (let i of res) {
          if (
            i["identifier"]
              .substring(2)
              .toUpperCase()
              .indexOf(this.keyWord.toUpperCase()) !== -1 ||
            i["name"]
            .toUpperCase()
            .indexOf(this.keyWord.toUpperCase()) !== -1
          ) {
            this.coincidences.push(i);
          }
        }
      });

    this.api
      .getElemets("http://localhost:3000/api/users")
      .subscribe((res: Object[]) => {
        for (let i of res) {
          if (i["uname"].toUpperCase().indexOf(this.keyWord.toUpperCase()) !== -1) {
            this.coincidences.push(i);
          }
        }
      });

    this.api
      .getElemets("http://localhost:3000/api/games")
      .subscribe((res: Object[]) => {
        for (let i of res) {
          if (i["title"].toUpperCase().indexOf(this.keyWord.toUpperCase()) !== -1) {
            this.coincidences.push(i);
          }
        }
      });

      this.coincidences.sort();
  }
}
