import { Component, OnInit, Output, EventEmitter} from "@angular/core";
import { Router } from "@angular/router";

@Component({
  selector: "app-navbar",
  templateUrl: "./navbar.component.html",
  styleUrls: ["./navbar.component.css"]
})
export class NavbarComponent implements OnInit {

  estaConectado: boolean = false;
  estaAbierto: boolean = false;
  @Output() activarMenu = new EventEmitter<boolean>();
  @Output() pulsarMenu = new EventEmitter<boolean>();

  constructor(private route: Router) {
    this.route.events.subscribe(() => {
      if (localStorage.getItem("_id") == null) {
        this.estaConectado = false;
      } else {
        this.estaConectado = true;
      }
      this.activarMenu.emit(this.estaConectado);
    });
    this.pulsarMenu.emit(this.estaAbierto);
  }

  ngOnInit() {
  }

  toggleMenu(){
    if (this.estaAbierto === true) {
      this.estaAbierto = false;
    } else {
      this.estaAbierto = true;
    }
    this.pulsarMenu.emit(this.estaAbierto);
  }

}
