import { Component, OnInit } from "@angular/core";
import { NgForm } from "@angular/forms";
import { characterModel } from "../../../../models/character.model";
import Swal from "sweetalert2";
import { APIServiceService } from "../../../../services/api-service.service";
import { gameRoomModel } from "../../../../models/game-room.model";
import { Router } from "@angular/router";
import { fichaModel } from "../../../../models/fichas.model";

@Component({
  selector: "app-my-bault",
  templateUrl: "./my-bault.component.html",
  styleUrls: ["./my-bault.component.css"]
})
export class MyBaultComponent implements OnInit {
  personaje: characterModel;
  ficha: fichaModel;
  partida: gameRoomModel;
  idUser: string;
  personajes: any[] = [];
  partidas: any[] = [];

  constructor(private charS: APIServiceService, private router: Router) {
    this.personaje = new characterModel();
    this.partida = new gameRoomModel();
    this.ficha = new fichaModel();
    this.personaje.npc = false;
    this.idUser = localStorage.getItem("_id");
    this.partida.id_user = this.idUser;
    this.personaje.id_user = this.idUser;
    this.partida.open = false;
    this.getAllCharacters();
    this.getAllGameRooms();
  }

  ngOnInit() {
    this.charS.refreshNedeed$.subscribe(() => {
      this.personajes = [];
      this.partidas = [];
      this.getAllCharacters();
      this.getAllGameRooms();
    });

    $(document).ready(function() {
      /* $(".body-secction").hide(); */
      $("#nuevoPer").hide();
      $("#nuevaPar").hide();
    });
  }

  crearPersonaje() {
    $("#nuevoPer").show(1000);
  }

  crearPartida() {
    $("#nuevaPar").show(1000);
  }

  cerrarP() {
    $(".nuevo").hide(1000);
  }

  onSubmitPersonaje(form: NgForm) {
    Swal.fire({
      allowOutsideClick: false,
      type: "info",
      text: "Espere por favor"
    });
    Swal.showLoading();

    if (form.invalid) {
      Swal.fire({
        allowOutsideClick: false,
        type: "error",
        text: "Los datos no están completos"
      });
      return;
    }

    this.personaje.identifier = "@" + this.personaje.identifier;

    this.charS
      .createCharacter(this.personaje, "http://localhost:3000/api/characters")
      .subscribe(res => {
        Swal.fire({
          allowOutsideClick: false,
          type: "success",
          text: "Se ha creado con éxito"
        });
      });
  }

  onSubmitPartida(form: NgForm) {
    Swal.fire({
      allowOutsideClick: false,
      type: "info",
      text: "Espere por favor"
    });
    Swal.showLoading();

    if (form.invalid) {
      Swal.fire({
        allowOutsideClick: false,
        type: "error",
        text: "Los datos no están completos"
      });
      return;
    }

    this.charS
      .createGameRoom(this.partida, "http://localhost:3000/api/games")
      .subscribe(res => {
        this.ngOnInit();
        Swal.fire({
          allowOutsideClick: false,
          type: "success",
          text: "Se ha creado con éxito"
        });
      });
  }

  getAllCharacters() {
    this.charS
      .getElemets("http://localhost:3000/api/characters")
      .subscribe((res: Object[]) => {
        for (let i of res) {
          if (i["id_user"] == this.idUser) {
            this.personajes.push(i);
          }
        }
      });
  }

  getAllGameRooms() {
    this.charS
      .getElemets("http://localhost:3000/api/games")
      .subscribe((res: Object[]) => {
        for (let i of res) {
          if (i["id_user"] === this.idUser) {
            this.partidas.unshift(i);
          }
          for (let a of i["players"]) {
            for (let b of this.personajes) {
              if (b["_id"] === a) {
                this.partidas.unshift(i);
              }
            }
          }
        }
      });
  }

  verPersonaje(id: string) {
    this.router.navigate(["/personaje/", id]);
  }

  verPartida(id: string) {
    this.router.navigate(["/partida/", id]);
  }
}
