import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-blog-category',
  templateUrl: './blog-category.component.html',
  styleUrls: ['../blog-home/blog-home.component.css']
})
export class BlogCategoryComponent implements OnInit {

  categoriaActual: string;
  category: string;

  constructor( private activatedRoute: ActivatedRoute, private router: Router ) { 
    this.activatedRoute.params.subscribe( params => {
      let term = params.category;
      if (term == 'news') {
        this.categoriaActual = 'novedades';
      } else if (term == 'articles') {
        this.categoriaActual = 'artículos';
      } else if (term == 'interviews') {
        this.categoriaActual = 'entrevistas';
      }
      this.category = term;
    });
  }

  ngOnInit() {
  }

  categoria( categoria: string ){
    return this.router.navigate(["/blog/", categoria]);
  }

  buscar( term: string ){
    this.router.navigate( ['/blog/search/', term] );
  }

}
