import { Component, OnInit, Input } from "@angular/core";
import { publicationModel } from "../../../models/publications.model";
import { APIServiceService } from "../../../services/api-service.service";
import { userModel } from "src/app/models/user.model";

@Component({
  selector: "app-publicacion",
  templateUrl: "./publicacion.component.html",
  styleUrls: ["./publicacion.component.css"]
})
export class PublicacionComponent implements OnInit {
  @Input() id: string;
  publication: publicationModel;

  nombre: string;
  img: string;
  ide: string;

  constructor(private api: APIServiceService) {
    this.publication = new publicationModel();
  }

  ngOnInit() {
    this.getData();
  }

  getData() {
    this.api
      .getElement(this.id, "http://localhost:3000/api/publications")
      .subscribe(res => {
        this.publication.content = res["Usuario"].content;
        this.publication.id_user = res["Usuario"].id_user;
        this.publication.id_character = res["Usuario"].id_character;
        this.publication.game_room_id = res["Usuario"].game_room_id;
        if (this.publication.game_room_id === " ") {
          this.getUser();
        } else {
          if ((this.publication.id_character === " ")) {
            this.getUser();
          } else {
            this.getChar();
          }
        }
      });
  }

  getUser() {
    this.api
      .getElement(this.publication.id_user, "http://localhost:3000/api/users")
      .subscribe(res => {
        this.nombre = res["Usuario"].uname;
        this.img = res["Usuario"].img;
        this.id = "/profile/"+res["Usuario"]._id; 
      });
  }

  getChar() {
    this.api
      .getElement(
        this.publication.id_character,
        "http://localhost:3000/api/characters"
      )
      .subscribe(res => {
        this.nombre = res["Usuario"].name;
        this.img = res["Usuario"].img;
        this.id = "/personaje/"+res["Usuario"]._id;
      });
  }
}
