import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { APIServiceService } from "../../../../services/api-service.service";
import { characterModel } from "../../../../models/character.model";
import { fichaModel } from "../../../../models/fichas.model";

@Component({
  selector: "app-characters",
  templateUrl: "./characters.component.html",
  styleUrls: ["./characters.component.css"]
})
export class CharactersComponent implements OnInit {
  idPersonaje: string;
  Personaje: characterModel;
  editable: boolean = false;
  disabled: string;
  usuarios: any[] = [];
  havFicha: boolean = false;
  ficha: fichaModel;

  constructor(
    private activatedRoute: ActivatedRoute,
    private api: APIServiceService
  ) {
    this.activatedRoute.params.subscribe(params => {
      this.idPersonaje = params.id;
      this.getPersonaje();
    });
    this.Personaje = new characterModel();
    this.ficha = new fichaModel();
  }

  ngOnInit() {
    $(document).ready(function() {
      $(".cambiarImagen").hide();
      $("#abrir").on("click", function() {
        $(".cambiarImagen").show(1);
      });
      $("#cerrar").on("click", function() {
        $(".cambiarImagen").hide(1);
      });
    });
  }

  getPersonaje() {
    this.api
      .getElement(this.idPersonaje, "http://localhost:3000/api/characters")
      .subscribe(res => {
        this.Personaje.name = res["Usuario"].name;
        this.Personaje.identifier = res["Usuario"].identifier;
        this.Personaje._id = res["Usuario"]._id;
        this.Personaje.file = res["Usuario"].file;
        this.Personaje.friendList = res["Usuario"].friendList;
        this.Personaje.id_user = res["Usuario"].id_user;
        this.Personaje.img = res["Usuario"].img;
        this.Personaje.npc = res["Usuario"].npc;
        this.disabled = "";
        if (this.Personaje.id_user == localStorage.getItem("_id")) {
          this.editable = true;
        } else {
          this.disabled = "disabled";
        }
        this.haveFicha();
      });
  }

  haveFicha() {
    if (this.Personaje.file === null || this.Personaje.file === undefined) {
      this.havFicha = false;
    } else {
      this.havFicha = true;
    }
  }

  addFile() {
    this.ficha.id_character = this.Personaje._id;
    this.ficha.id_user = this.Personaje.id_user;
    this.ficha.title = "Sin título";
    this.api
      .createfichas(this.ficha, "http://localhost:3000/api/files")
      .subscribe(res => {
        this.havFicha = true;
        this.api
          .getElemets("http://localhost:3000/api/files")
          .subscribe((res: Object[]) => {
            for (let a of res) {
              if (a["id_character"] == this.Personaje._id) {
                this.Personaje.file = a["_id"];
                this.save();
              }
            }
          });
      });
  }

  cambiarImagen(url: string) {
    this.Personaje.img = url;
    this.save();
  }

  saveChanges() {
    this.Personaje.name = $("#nombre").html();
    this.Personaje.identifier = $("#identificador").html();
    if ($("#npc").is(":Checked")) {
      this.Personaje.npc = true;
    } else {
      this.Personaje.npc = false;
    }

    this.api
      .getElemets("http://localhost:3000/api/characters")
      .subscribe((res: Object[]) => {
        for (let i of res) {
          if (i["identifier"] == this.Personaje.identifier) {
            if (i["_id"] != this.Personaje._id) {
              return;
            }
          }
        }
      });

    this.save();
  }

  save() {
    this.api
      .modifyCharacter(this.Personaje, "http://localhost:3000/api/characters")
      .subscribe(() => {});
  }
}
