import { Component } from "@angular/core";
import * as $ from "jquery";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"]
})
export class AppComponent {

  mostrarMenu: boolean;
  toggle: boolean = false;

  ngOnInit() {

    $(document).ready(function() {
      /* AÑADIR PUBLICACIÓN */
      $(".long").hide();
      $("#states").addClass("color-active");

      $("#states").on("click", function() {
        $("#states").addClass("color-active");
        $("#longe").removeClass("color-active");
        $(".long").slideUp();
        $(".estate").slideDown();
      });
      $("#longe").on("click", function() {
        $("#states").removeClass("color-active");
        $("#longe").addClass("color-active");
        $(".estate").slideUp();
        $(".long").slideDown();
      });
      /* AÑADIR PUBLICACIÓN */

      /* LOGIN - SING UP */

      $(".singup-container-c").hide();

      $(".singup-cliker").on("click", function() {
        $(".secction-login-c").css("min-height", "110vh");
        $(".singup-container-c").slideDown("slow");
      });

      $(".login-cliker").on("click", function() {
        $(".singup-container-c").slideUp("slow");
        setTimeout(function() {
          $(".secction-login-c").css("min-height", "80vh");
        }, 500);
      });

      /* LOGIN - SING UP */
      
    });
  }

  showMenu(event: boolean) {
    this.mostrarMenu = event;
  }

  pulsadoMenu(event: boolean) {
    this.toggle = event;
  }
}
