import { Component, OnInit } from "@angular/core";
import { APIServiceService } from "../../../services/api-service.service";
import { ActivatedRoute } from "@angular/router";
import { userModel } from '../../../models/user.model';

@Component({
  selector: "app-chat",
  templateUrl: "./chat.component.html",
  styleUrls: ["./chat.component.css"]
})
export class ChatComponent implements OnInit {
  idActualChat: string;
  idActualUser: string;
  ActualUser: userModel;
  ChatsArray: chatData[] = [];
  chatForm: chatData;
  openChats: any[] = [];

  constructor(private api: APIServiceService, private Arout: ActivatedRoute) {
    this.Arout.params.subscribe(res => {
      this.idActualChat = res["id"];
    });
    this.ActualUser = new userModel();
    this.getAllChats();
    this.getUser();
  }

  ngOnInit() {}

  getAllChats() {
    this.idActualUser = localStorage.getItem("_id");
    this.api
      .getElemets("http://localhost:3000/api/chatrooms")
      .subscribe((res: Object[]) => {
        for (let a of res) {
          for(let b of a["id_users"]){
            if(b == this.idActualUser){
              this.openChats.push(a);
            }
          }
        }
        for(let a of this.openChats){
          for(let b of a["id_users"]){
            if(b != this.idActualUser){
              this.api.getElement(b, "http://localhost:3000/api/users")
              .subscribe( res =>{
                this.chatForm = new chatData();
                this.chatForm.username = res['Usuario'].uname;
                this.chatForm.idChatRoom = a['_id'];
                this.chatForm.idUser = res['Usuario']._id;
                this.chatForm.img = res['Usuario'].img;
                this.ChatsArray.push(this.chatForm);
              });
            }
          }
        }
      });
  }

  getUser(){
    this.api.getElement(this.idActualUser,  "http://localhost:3000/api/users")
    .subscribe( res =>{
      this.ActualUser._id = res['Usuario']._id;
      this.ActualUser.uname = res['Usuario'].uname;
      this.ActualUser.img = res['Usuario'].img;
    });
  }
}

class chatData{
  idUser: string;
  username: string;
  idChatRoom: string;
  img: string;
}
